import React, { Fragment, useEffect, useState } from 'react';
import {
  Container,
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  Button,
} from 'reactstrap';

import { withRouter } from 'react-router';
import { RouteComponentProps } from 'react-router';

import registerServiceWorker from '../../services/sw/registerServiceWorker';

type Props = {
  children: any;
} & RouteComponentProps;

function PrivateLayout({ history, location, match, children }: Props) {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);

  // #region on mount effect
  useEffect(() => {
    if (typeof window !== undefined) {
      // register service worker (no worry about multiple attempts to register, browser will ignore when already registered)
      registerServiceWorker();
    }
  }, []);
  // #endregion

  return (
    <Fragment>
      <Container>
        <Navbar color="light" light expand="md">
          <NavbarBrand className="mr-5" href="/">
            <b>fabrika</b>
          </NavbarBrand>
          <NavbarToggler onClick={toggle} />
          <Collapse isOpen={isOpen} navbar>
            <Nav className="mr-auto ml-5" navbar>
              <NavItem>
                <Button outline color="secondary" href="#" className="mr-5">
                  New calculation
                </Button>
              </NavItem>
              <NavItem>
                <NavLink href="#">Orders</NavLink>
              </NavItem>
            </Nav>
            <Button color="link" className="text-secondary" href="#">
              avenir.babin@mehanika.studio
            </Button>
          </Collapse>
        </Navbar>
        {children}
      </Container>
    </Fragment>
  );
}

export default withRouter(PrivateLayout);
