import React, { useCallback, useState } from 'react';
import { RouteComponentProps } from 'react-router';
import { Card, Row, Col } from 'reactstrap';
import PrivateLayout from '../../layout/privateLayout';
import { OwnProps, MappedStateToProps, MappedDispatchToProps } from './index';
import { useDropzone } from 'react-dropzone';
import { withRouter } from 'react-router';

import { DD, DDTitle } from './styled/style';

// #region types
export type Props = RouteComponentProps &
  MappedDispatchToProps &
  MappedStateToProps &
  OwnProps;
// #endregion

function Onboarding(props: Props) {
  const [files, setFiles] = useState([]);

  const onDrop = useCallback(files => {
    props.history.push('/element');
    setFiles(files);
  }, []);
  const { getRootProps, getInputProps, isDragActive } = useDropzone({ onDrop });

  return (
    <PrivateLayout>
      <Card className="mt-5">
        <div {...getRootProps()}>
          <input {...getInputProps()} />
          <DD>
            <Row>
              <Col md="2" />
              <Col md="7">
                {isDragActive ? (
                  <DDTitle>Drop the files here...</DDTitle>
                ) : (
                  <p>
                    <DDTitle>Drop parts here</DDTitle>
                    <div>or select files</div>
                  </p>
                )}
              </Col>
              <Col md="3" />
            </Row>
          </DD>
        </div>
      </Card>
      <Row className="mt-5">
        <Col md="2" />
        <Col md="10">
          <div>
            please, check inside radii &gt; 1 mm, walls &gt; 1 mm, minimum holes is 1
            mm
          </div>
          <div>all uploads are secure and confidential</div>
        </Col>
      </Row>
    </PrivateLayout>
  );
}

Onboarding.displayName = 'Onboarding';

export default withRouter(Onboarding);
