import React, { useState, useEffect, useCallback } from 'react';
import { RouteComponentProps } from 'react-router';
import {
  Col,
  Button,
  Form,
  FormGroup,
  Label,
  Input,
  FormText,
  Row,
} from 'reactstrap';

import auth from '../../services/auth';
import MainLayout from '../../layout/mainLayout';
import { MappedDispatchToProps, MappedStateToProps, OwnProps } from './index';

// #region types
export type Props = RouteComponentProps &
  MappedStateToProps &
  MappedDispatchToProps &
  OwnProps;
// #endregion

function Registration({
  location,
  isLogging,
  isFetching,
  disconnectUser,
  logUserIfNeeded,
  history,
}: Props) {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  useEffect(() => {
    // on mount disconnect (clear previous user auth and info):
    disconnectUser();
  }, []);

  // #region form inputs change callbacks
  const handlesOnEmailChange = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>) => {
      if (event) {
        event.preventDefault();
        setEmail(event.target.value.trim());
      }
    },
    [],
  );

  const handlesOnPasswordChange = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>) => {
      if (event) {
        event.preventDefault();
        setPassword(event.target.value.trim());
      }
    },
    [],
  );
  // #endregion

  // #region on login button click callback
  const handlesOnLogin = useCallback(
    async (event: React.MouseEvent<HTMLButtonElement>) => {
      event && event.preventDefault();

      try {
        const response = await logUserIfNeeded(email, password);
        const {
          data: { token, user },
        } = response.payload;

        auth.setToken(token);
        auth.setUserInfo(user);

        // @ts-ignore
        const { from } = location.state || { from: { pathname: '/' } };

        history.replace(from); // back to Home
      } catch (error) {
        /* eslint-disable no-console */
        console.log('login went wrong..., error: ', error);
        /* eslint-enable no-console */
      }
    },
    [history, location, email, password],
  );
  // #endregion

  // #region on go back home button click callback
  const goHome = useCallback(
    (event: React.MouseEvent<HTMLButtonElement>) => {
      event && event.preventDefault();
      history.push({ pathname: '/' });
    },
    [history],
  );
  // #endregion

  return (
    <MainLayout>
      <Row>
        <Col md="6">
          <h2 className="pt-5 pb-3">Sign Up</h2>
          <p>Account will allow you to track your order status online </p>
          <p>
            Already have an account? <a href="/">Log in</a>
          </p>
          <Form className="p-3">
            <FormGroup row>
              <Label for="inputUsername">Username</Label>
              <Input
                type="text"
                name="username"
                id="inputUsername"
                placeholder=""
              />
            </FormGroup>
            <FormGroup row>
              <Label for="inputEmail">Email</Label>
              <Input type="email" name="email" id="inputEmail" placeholder="" />
            </FormGroup>
            <FormGroup row>
              <Label for="inputPassword">Password</Label>
              <Input
                type="password"
                name="password"
                id="inputPassword"
                placeholder=""
              />
            </FormGroup>
            <FormGroup row>
              <Label for="inputPasswordRepeat">Password repeat</Label>
              <Input
                type="password"
                name="password"
                id="inputPasswordRepeat"
                placeholder=""
              />
            </FormGroup>
            <FormGroup row>
              <Col>
                <FormGroup check>
                  <Label check>
                    <Input type="checkbox" id="checkbox" /> I agree with the
                    terms of use
                  </Label>
                </FormGroup>
              </Col>
            </FormGroup>
          </Form>
          <Button outline disabled color="secondary" className="mt-2">
            Sign Up
          </Button>
        </Col>
        <Col md="6" />
      </Row>
    </MainLayout>
  );
}

Registration.defaultProps = {
  isFetching: false,
  isLogging: false,
};

Registration.displayName = 'Registration';

export default Registration;
