import React from 'react';
import { Route, Switch } from 'react-router';
import {
  Home as AsyncHome,
  About as AsyncAbout,
  PageNotFound as AsyncPageNotFound,
  Onboarding as AsyncOnboarding,
  Registration as AsyncRegistration,
  Element as AsyncElement,
  Elements as AsyncElements,
  Orders as AsyncOrders,
  Profile as AsyncProfile,
} from './routes';
import PrivateRoute from '../components/privateRoute';

const MainRoutes = () => {
  return (
    <Switch>
      {/* public views: */}
      <Route exact path="/">
        <AsyncHome />
      </Route>

      <Route exact path="/registration">
        <AsyncRegistration />
      </Route>

      <Route path="/about">
        <AsyncAbout />
      </Route>

      <Route path="/element">
        <AsyncElement />
      </Route>

      <Route path="/elements">
        <AsyncElements />
      </Route>

      {/* private views: need user to be authenticated */}
      <PrivateRoute path="/onboarding">
        <AsyncOnboarding />
      </PrivateRoute>

      <PrivateRoute path="/orders">
        <AsyncOrders />
      </PrivateRoute>

      <PrivateRoute path="/profile">
        <AsyncProfile />
      </PrivateRoute>

      <Route path="*">
        <AsyncPageNotFound />
      </Route>
    </Switch>
  );
};

export default MainRoutes;
