import React, { useState, useEffect, useCallback, useContext } from 'react';
import { useHistory, useLocation } from 'react-router-dom';
import { RouteComponentProps } from 'react-router';
import { Button, Form, FormGroup, Label, Input, Row, Col } from 'reactstrap';
import MainLayout from '../../layout/mainLayout';
import { appConfig } from '../../config/appConfig';
import { AuthProviderState, AuthContext } from '../../contexts/auth';
import { getLocationOrigin, jsonHeader } from '../../services/API/fetchTools';
import axios from 'axios';

import { MappedDispatchToProps, MappedStateToProps, OwnProps } from './index';

// #region  types
export type Props = RouteComponentProps &
  MappedDispatchToProps &
  MappedStateToProps &
  OwnProps;
// #endregion

function Home(props: Props) {
  const location = useLocation();
  const history = useHistory();
  const [username, setUsername] = useState<string>('dmitry');
  const [password, setPassword] = useState<string>('729082app');
  const [isLogging, setIsLogging] = useState<boolean>(false);
  const auth = useContext<AuthProviderState | null>(AuthContext);

  const handlesOnLogin = useCallback(
    async (event: React.MouseEvent<HTMLButtonElement>) => {
      event && event.preventDefault();
      try {
        setIsLogging(true);
        const response = await logUser(username, password);
        const {
          data: { key, user },
        } = response;

        console.log('user fetched: ', { key, user });

        auth?.setToken(key);
        auth?.setUserInfo(user);

        history.push('/onboarding');
      } catch (error) {
        /* eslint-disable no-console */
        console.log('login went wrong..., error: ', error);
        /* eslint-enable no-console */
      } finally {
        setIsLogging(false);
      }
    },
    [history, username, password, location],
  );

  const handlesOnUsernameChange = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>) => {
      event && event.preventDefault();
      // should add some validator before setState in real use cases
      setUsername(event.target.value.trim());
    },
    [],
  );

  const handlesOnPasswordChange = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>) => {
      event && event.preventDefault();
      // should add some validator before setState in real use cases
      setPassword(event.target.value.trim());
    },
    [],
  );

  // #region log user request
  const logUser = useCallback(
    async (
      email: string = '',
      password: string = '',
    ): Promise<{ data: { user: User; key: string } }> => {
      const __SOME_LOGIN_API__ = 'login';
      const url = `${getLocationOrigin()}/${__SOME_LOGIN_API__}/`;
      const method = 'post';
      const headers = jsonHeader;
      const options = {
        data: {
          username,
          password,
        },
      };

      try {
        const response = await axios.request<{ key: string; user: any }>({
          method,
          url,
          headers: {
            ...headers,
          },
          ...options,
        });
        return response;
      } catch (error) {
        throw error;
      }
    },
    [],
  );
  // #endregion

  return (
    <MainLayout>
      <Row>
        <Col md="6">
          <h2 className="pt-5 pb-3">Sign In</h2>
          <p>Account will allow you to track your order status online.</p>
          <p>
            Don’t have an account yet?{' '}
            <a href="/registration">Create an account</a>
          </p>
          <Form className="p-3">
            <FormGroup row>
              <Label for="inputUsername">Username</Label>
              <Input
                type="text"
                name="username"
                id="inputUsername"
                placeholder=""
                value={username}
                onChange={handlesOnUsernameChange}
              />
            </FormGroup>
            <FormGroup row>
              <Label for="inputPassword">Password</Label>
              <Input
                type="password"
                name="password"
                id="inputPassword"
                value={password}
                onChange={handlesOnPasswordChange}
                placeholder=""
              />
            </FormGroup>
          </Form>
          <Button
            outline
            color="secondary"
            onClick={handlesOnLogin}
            className="mt-2"
          >
            Sign In
          </Button>
        </Col>
        <Col md="6" />
      </Row>
    </MainLayout>
  );
}

Home.displayName = 'Home';

export default Home;
